var namesEnglish = maleNamesEnglish.concat(femaleNamesEnglish);
var namesSpanish = maleNamesSpanish.concat(femaleNamesSpanish);
var names = namesEnglish.concat(namesSpanish);
var lastNames = lastNamesEnglish.concat(lastNamesSpanish);

function randArray(myArray) {
 return myArray[Math.floor(Math.random() * myArray.length)];
}

function removeByIndex(array, index) {
  return array.filter(function (el, i) {
    return index !== i;
  });
}
  
function generate_name() {
  return randArray(names)+" "+randArray(lastNames);
}

function generate_name_spanish() {
  return randArray(namesSpanish)+" "+randArray(lastNamesSpanish);
}

function generate_name_english() {
  return randArray(namesEnglish)+" "+randArray(lastNamesEnglish);
}

function generate_show_name(id) {
  var name = '';
  var number = $('#name_number').val();
  var el = document.querySelector("#"+id);
  el.innerHTML = '';
  for( var i=0; i<number ; i++) {
    if ($('#language').val() == 'spanish') {
      name = generate_name_spanish();
    } else if ($('#language').val() == 'english') {
      name = generate_name_english();
    } else {
      name = generate_name();
    }
    el.innerHTML += name + '<br>';
  }
}

function shuffle(a) {
    var j, x, i;
    for (i = a.length; i; i--) {
        j = Math.floor(Math.random() * i);
        x = a[i - 1];
        a[i - 1] = a[j];
        a[j] = x;
    }
}

