export default function generate(lang) {
  var name = '';
  if (lang == 'spanish') {
    name = generate_name_spanish();
  } else if (lang == 'english') {
    name = generate_name_english();
  } else {
    name = generate_name();
  }
  var concept = randArray(concepts);
  var aspects_aux = aspects.slice(0);
  var good_aux = good.slice(0);
  var bad_aux = bad.slice(0);
  var desires_aux = desires.slice(0);
  var poles_aux = poles.slice(0);

  shuffle(aspects_aux)
  shuffle(good_aux)
  shuffle(bad_aux)
  shuffle(desires_aux)
  shuffle(poles_aux)

  var a1 = aspects_aux.pop()
  var a2 = aspects_aux.pop()
  var g1 = good_aux.pop()
  var g2 = good_aux.pop()
  var b1 = bad_aux.pop()
  var b2 = bad_aux.pop()
  var d1 = desires_aux.pop()
  var p1 = poles_aux.pop()
  var p2 = poles_aux.pop()
  var el =  "";
  el += "<b>Nombre:</b> "+name+"<br>";
  el += "<b>Concepto:</b> "+concept+"<br>";
  el += "<b>Aspectos:</b> "+a1+" - "+a2+"<br>";
  el += "<b>Fortalezas:</b> "+g1+" - "+g2+"<br>";
  el += "<b>Complicaciones:</b> "+b1+" - "+b2+"<br>";
  el += "<b>Polos:</b> "+p1+" / "+p2+"<br>";
  el += "<b>Deseo:</b> "+d1+"<br>";
  
  return el;
}



